import '../scss/app.scss'
import '../pug/index.pug'
import { debounce, throttle } from './utils'

let nose = document.querySelector('.nose')
let hands = document.querySelectorAll('.hands button')
let gritometer = document.getElementById('gritometer')
let hand_selection = document.getElementById('hand')

let timers = []
let keys = new Event('keyup')

const speed_up = 1
const speed_down = 0.5
const interval = 50

nose.addEventListener('mousedown', (e) => {
  timers.forEach((timer) => {
    clearTimeout(timer)
  })

  timers.push(
    setInterval(() => {
      let value = Number(gritometer.value)

      if (value < 100) {
        gritometer.value = value + speed_up
        gritometer.dispatchEvent(keys)
      } else {
        document.querySelectorAll('.win').forEach((el) => {
          el.classList.remove('hidden')
        })
      }
    }, interval)
  )
})

nose.addEventListener('mouseup', (e) => {
  timers.forEach((timer) => {
    clearTimeout(timer)
  })

  timers.push(
    setInterval(() => {
      let value = Number(gritometer.value)

      if (value > 0 && value != 100) {
        gritometer.value = value - speed_down
        gritometer.dispatchEvent(keys)
      } else if (value >= 100) {
        document.querySelectorAll('.win').forEach((el) => {
          el.classList.remove('hidden')
        })
      }
    }, interval)
  )
})

hands.forEach((hand) => {
  hand.addEventListener('click', (el) => {
    hand_selection.value = el.target.dataset.hand
    hand_selection.dispatchEvent(keys)

    nose.classList.remove(
      'hand-default',
      'hand-l',
      'hand-ml',
      'hand-m',
      'hand-md',
      'hand-d'
    )

    nose.classList.add(`hand-${hand_selection.value}`)
  })
})
