export function debounce(func, wait, immediate) {
  var timeout
  return function () {
    var context = this,
      args = arguments
    var later = function () {
      timeout = null
      if (!immediate) func.apply(context, args)
    }
    var callNow = immediate && !timeout
    clearTimeout(timeout)
    timeout = setTimeout(later, wait)
    if (callNow) func.apply(context, args)
  }
}

export function throttle(callback, limit) {
  var waiting = false
  return function () {
    if (!waiting) {
      callback.apply(this, arguments)
      waiting = true
      setTimeout(function () {
        waiting = false
      }, limit)
    }
  }
}

export function bind(event) {
  let elem = document.querySelectorAll(
    `[data-model='${event.currentTarget.id}']`
  )

  for (var key in elem) {
    let type = elem[key].nodeName

    if (type == 'PROGRESS' || type == 'INPUT') {
      elem[key].value = event.currentTarget.value
    } else if (type == 'P' || type == 'DIV') {
      elem[key].innerHTML = event.currentTarget.value
    }
  }
}

let models = document.querySelectorAll('.model-source')

if (models) {
  models.forEach((el) => {
    el.addEventListener('keyup', bind)
  })
}
